* {
  box-sizing: border-box; }

body {
  margin: 0;
  padding: 0;
  min-width: 375px;
  overflow-x: hidden; }

h2,
h3,
p {
  margin: 0; }

ul,
img,
figure {
  margin: 0;
  padding: 0; }

ul {
  list-style: none; }

@font-face {
  font-family: "Inter-Bold";
  font-weight: normal;
  font-style: normal;
  src: url("../fonts/Inter-Bold.woff2") format("woff2"), url("../fonts/Inter-Bold.woff") format("woff"); }

@font-face {
  font-family: "Inter-SemiBold";
